import { useEffect } from "react";
import { Link, Navigate, useParams } from "react-router-dom";
import {ResetcurrentOrder} from '../features/Order/OrderSlice'
import { useDispatch, useSelector } from "react-redux";
import { ResetcartAsync } from "../features/Cart/cartSlice";
import { selectuser } from "../features/Auth/AuthSlice";
import {resetCurrentOrder} from '../features/Order/OrderSlice'
export default function OrderSuccess() {
    const params=useParams();
    const dispatch=useDispatch();
    const user=useSelector(selectuser)
    useEffect(()=>{
      dispatch(ResetcartAsync())
      dispatch(resetCurrentOrder())
    },[dispatch])
    return (
      <>
    { !params.id && <Navigate
 to='/' replace={true}></Navigate>}

        <main className="grid min-h-full place-items-center bg-white px-6 py-24 sm:py-32 lg:px-8">
          <div className="text-center">
            <p className="text-base font-semibold text-indigo-600">🎁 Order Placed </p>
            <h1 className="mt-4 text-3xl font-bold tracking-tight text-gray-900 sm:text-5xl">Your Order has  been Placed 🐱‍🏍</h1>
            <h3 className="mt-4 text-3xl font-bold tracking-tight text-gray-900 sm:text-5xl">Your Order Id is #{params.id}</h3> 
            <p className="mt-6 text-base leading-7 text-gray-600">{'You can see your orders in my Account->My Order'} </p>
            <div className="mt-10 flex items-center justify-center gap-x-6">
              <Link
                to={'/'}
                className="rounded-md bg-indigo-600 px-3.5 py-2.5 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
              >
                Go back home
              </Link>
              
            </div>
          </div>
        </main>
      </>
    )
  }
  