import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import { Adduser,Updateuser,checkUser,loginUser,signout } from './AuthAPI';

const initialState = {
  loggedinuser: null,
  status: 'idle',
  error:null,
  userchecked:false
};

export const signOutAsync=createAsyncThunk(
  'auth/signOut',
  async(userid)=>{
    const response =signout(userid);
    return response.data;
  }
)
export const AdduserAsync = createAsyncThunk(
  'auth/Adduser',
  async (userdata) => {
    const response = await Adduser(userdata);
    return response.data;
  }
);
export const UpdateuserAsync = createAsyncThunk(
  'auth/Updateuser',
  async (userdata) => {
    const response = await Updateuser(userdata);
    return response.data;
  }
);

export const LoginUserAsync = createAsyncThunk(
  'auth/loginUser',
  async (loginData) => {
    const response = await loginUser(loginData);
    return response.data;
  }
);
export const checkUserAsync = createAsyncThunk(
  'auth/checkUser',
  async () => {
    const response = await checkUser();
    return response.data;
  }
);

export const counterSlice = createSlice({
  name: 'auth',
  initialState,
 
  reducers: {
    increment: (state) => {
      state.value += 1;
    },
  },

  extraReducers: (builder) => {
    builder
      .addCase(AdduserAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(AdduserAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.loggedinuser= action.payload;
      })
      .addCase(UpdateuserAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(UpdateuserAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.loggedinuser= action.payload;
      })
      .addCase(signOutAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(signOutAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.loggedinuser= null;
      })
      .addCase(LoginUserAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(LoginUserAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.loggedinuser= action.payload;
      })
      .addCase(LoginUserAsync.rejected, (state, action) => {
        state.status = 'idle';
        state.error= action.error;
      })
      .addCase(checkUserAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(checkUserAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.loggedinuser= action.payload;
        state.userchecked=true;
      })
      .addCase(checkUserAsync.rejected, (state, action) => {
        state.status = 'idle';
        state.error= action.error;
        state.userchecked=true;
      });
  },
});

export const { increment } = counterSlice.actions;
export const selectuser = (state) => state.auth.loggedinuser;
export const seterror=(state)=>state.auth.error;
export const setcheckUser=(state)=>state.auth.userchecked;

export default counterSlice.reducer; 
