import React, { useEffect } from 'react';
import {
  createBrowserRouter,
  RouterProvider,
} from "react-router-dom";
import Home from "./pages/home"
import Loginpage from './pages/login';

import Cart from './features/Cart/Cart';
import SignupPage from './pages/signup';
import Checkoutpage from './pages/Checkout';

import Productfeaturespage from './pages/productfeaturespage';
import Protected from './features/Auth/protected';
import { useDispatch, useSelector } from 'react-redux';
import { cartProductsByUserIdAsync } from './features/Cart/cartSlice';
//import { selectUserInfo } from './features/Auth/AuthSlice';

import Errorpage from './pages/error';
import OrderSuccess from './pages/OrderSuccess';

import Orderspage from './pages/Orderspage';
import UserProfilePage from './pages/UserProfilePage'

import { fetchLoggedInfoUserAsync, selectUserInfo } from './features/user/userSlice';
import Logout from './pages/logout';
import ForgotPassword from './pages/forgotpassword';
import AdminProtected from './features/Auth/AdminProtected';
import AdminProductfeaturespage from './pages/Adminproductfeaturespage';
import AdminHome from './pages/AdminHome';
import ProductFormPage from './pages/ProductFormPage';
import AdminordersPage from './pages/AdminordersPage';
import { positions, Provider } from "react-alert";
import AlertTemplate from "react-alert-template-basic";
import { checkUserAsync, selectuser, setcheckUser } from './features/Auth/AuthSlice';
const router = createBrowserRouter([
  {
    path: "/",
    element: (
      <Protected>
         <Home/>
      </Protected>
    ),
  },
  {
    path: "/login",
    element:
     <Loginpage/>
  },
  {
    path: "/signup",
    element: <SignupPage/>
  },
  {
    path:"/cart",
    element:
    <Protected>
    <Cart/>
 </Protected>
  },
  {
    path:'/checkout',
    element:
    <Protected>
    <Checkoutpage/>
    </Protected>
  },
  {
    path: '/product-detail/:id',
    element:   <Protected>
    <Productfeaturespage/>
 </Protected>
  },
  {
    path: '/profile',
    element: <Protected>
    <UserProfilePage/>
 </Protected>
  },
  {
    path: '/product-success/:id',
    element: <Protected>
    <OrderSuccess/>
 </Protected>
  },
  { 
    path: '/myorders',
    element:   <Protected>
    <Orderspage/>
 </Protected>
  },
  {
    path: '/logout',
    element:  
    <Logout/>
  },
  {
    path: '/forgotpassword',
    element:  
    <ForgotPassword/>
  },
  {
    path: '/admin/product-detail/:id',
    element:  
    <AdminProtected>
      <AdminProductfeaturespage/>
    </AdminProtected>
  },
  {
    path: '/admin',
    element:  
    <AdminProtected>
      <AdminHome/>
    </AdminProtected>
  },
  {
    path: '/admin/addproduct',
    element:  
    <AdminProtected>
      <ProductFormPage/>
    </AdminProtected>
  },
  {
    path: '/admin/editproduct/:id',
    element:  
    <AdminProtected>
      <ProductFormPage/>
    </AdminProtected>
  },
  {
    path: '/admin/orders',
    element:  
    <AdminProtected>
      <AdminordersPage/>
    </AdminProtected>
  },
  {
    path: '*',
    element:  <Errorpage></Errorpage>
  }
  
]);
const options = {
  position: positions.TOP_LEFT, // Update the position to TOP_LEFT
  timeout: 5000,
  offset: '30px',
  transition: 'scale',
  type: 'success', // Set the default alert type
  containerStyle: {
    zIndex: 1000,
    left: 0, // Set left position to 0
  },
  containerClassName: 'custom-alert-container',
  alertStyle: {
    fontSize: '16px',
    borderRadius: '8px',
    padding: '16px',
    marginBottom: '8px',
    minWidth: '300px',
  },
  titleStyle: {
    fontSize: '18px',
    fontWeight: 'bold',
  },
  messageStyle: {
    fontSize: '16px',
  },
};

function App() {
  const user = useSelector(selectuser);
  const checkUser = useSelector(setcheckUser);
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(checkUserAsync());
  }, [dispatch]); // Fix: use curly braces instead of square brackets

  useEffect(() => {
    if (user) {
      dispatch(cartProductsByUserIdAsync());
      dispatch(fetchLoggedInfoUserAsync());
    }
  }, [dispatch,user]); // Fix: use curly braces instead of square brackets

  return (
    <>
      {checkUser && (
        <div className="App">
          <Provider template={AlertTemplate} {...options}>
            <RouterProvider router={router} />
          </Provider>
        </div>
      )}
    </>
  );
}

export default App;
