import { Fragment, useEffect, useState } from 'react'
import { Dialog, Transition } from '@headlessui/react'
import { XMarkIcon } from '@heroicons/react/24/outline'
import { Link, Navigate } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import { deleteCartItemAsync, setCartItems, updateCartItem, updateCartItemAsync } from './cartSlice'
import { discountedprice } from '../../constant'
import Modal from '../../common/modal'


export default function Cart() {
const items=useSelector(setCartItems);
let totalProducts=0;
let totalAmount=0;
const [showmodal,setshowmodal]=useState(null)
for(let i=0;i<items.length;i++)
{
  totalAmount+=Number(items[i].quanity)*Number(discountedprice(items[i].product));
  totalProducts+=Number(items[i].quanity)
}
const dispatch=useDispatch();
function handlechnage(e,item){
  dispatch(updateCartItemAsync({id:item.id,quanity:+e.target.value}))

}
function handledelete(e,product){
  dispatch(deleteCartItemAsync(product),[e,dispatch])
}
return ( 
<>
{items.length===0 && <Navigate
 to='/' replace={true}></Navigate>}

<div className="mx-auto max-w-2xl px-4 py-6 sm:px-6 sm:py-4 lg:max-w-7xl lg:px-8 bg-white mt-8">
<h1 className="text-3xl font-bold tracking-tight text-gray-900 ">Cart</h1>
<div className="mt-8">
          <div className="flow-root">
            <ul role="list" className="-my-6 divide-y divide-gray-200">
              {items.map((item) => (
                <li key={item.product.id} className="flex py-6">
                  <div className="h-24 w-24 flex-shrink-0 overflow-hidden rounded-md border border-gray-200">
                    <img
                      src={item.product.thumbnail}
                      alt={item.product.description}
                      className="h-full w-full object-cover object-center"
                    />
                  </div>

                  <div className="ml-4 flex flex-1 flex-col">
                    <div>
                      <div className="flex justify-between text-base font-medium text-gray-900">
                        <h3>
                          <div href='#'>{item.product.title}</div>
                        </h3>
                        <p className="ml-4">{discountedprice(item.product)}</p>
                      </div>
                      <p className="mt-1 text-sm text-gray-500">{item.product.color}</p>
                    </div>
                    <div className="flex flex-1 items-end justify-between text-sm">
                      <div className='flex justify-start  items-center'>
                      <div className="text-gray-500">
                      <label htmlFor="quantity" className="inline text-sm font-medium leading-6 text-gray-900">
            Qty
          </label>
                      <select className='text-sm m-2 py-1 pr-8' onChange={e=>handlechnage(e,item)} value={item.quanity}> 
                      <option value="1">1</option>
                            <option value="2">2</option>
                            <option value="3">3</option>
                            <option value="4">4</option>
                            <option value="5">5</option>
                      </select>
                      </div>
                     
                      </div>
                      
                      <div className="flex">
                       { <Modal title={`Delete ${item.product.title}`}
                        dangeraction={e=>handledelete(e,item)} 
                        dangeroption={'Delete'}
                        show={item.product.id===showmodal}
                        canceloption={'Cancel'}
                        message={`You sure you want to delete ${item.product.title}`}
                        cancelaction={()=>setshowmodal(null)}
                        />
               }
                        <button
                          type="button"
                          className="font-medium text-indigo-600 hover:text-indigo-500"
                          onClick={e=>{setshowmodal(item.product.id)}}
                        >
                          Remove
                        </button>
                      </div>
                    </div>
                  </div>
                </li>
              ))}
            </ul>
          </div>
        </div>
        <div className="border-t border-gray-200 px-4 py-6 sm:px-6">
        <div className="flex justify-between text-base font-medium text-gray-900">
          <p>Subtotal</p>
          <p>${totalAmount}</p>
        </div>
        <div className="flex justify-between text-base font-medium text-gray-900">
          <p>Products</p>
          <p>{totalProducts} Products</p>
        </div>
        <p className="mt-0.5 text-sm text-gray-500">Shipping and taxes calculated at checkout.</p>
        <div className="mt-6">
          <Link
            to={'/checkout'}
            className="flex items-center justify-center rounded-md border border-transparent bg-indigo-600 px-6 py-3 text-base font-medium text-white shadow-sm hover:bg-indigo-700 w-40"
          >
            Checkout
          </Link>
        </div>
        <div className="mt-6 flex justify-start text-center text-sm text-gray-500">
          <p>
            or
            <Link to={'/'}  className="font-medium text-indigo-600 hover:text-indigo-500 ml-4">
              Continue Shopping
              <span aria-hidden="true"> &rarr;</span>
            </Link>
           
          </p>
        </div>
      </div>
</div>
</>
  )
}
