export function addCartItem(item) {
  return new Promise(async (resolve) => {
    const response = await fetch("/cart", {
      method: "POST",
      body: JSON.stringify(item),
      headers: {
        "Content-type": "application/json; charset=UTF-8",
      },
    });

    const data = await response.json();
    resolve({ data });
  });
}
export function cartProductsByUserId() {
  return new Promise(async (resolve) => {
    const response = await fetch("/cart");
    const data = await response.json();
    resolve({ data });
  });
}
export function updateCartItem(item) {
  return new Promise(async (resolve) => {
    const response = await fetch("/cart/" + item.id, {
      method: "PATCH",
      body: JSON.stringify(item),
      headers: {
        "Content-type": "application/json; charset=UTF-8",
      },
    });

    const data = await response.json();
    resolve({ data });
  });
}
export function deleteCartItem(item) {
  return new Promise(async (resolve) => {
    await fetch("/cart/" + item.id, {
      method: "DELETE",
      headers: {
        "Content-type": "application/json; charset=UTF-8",
      },
    });

    resolve({ data: { id: item.id } });
  });
}
export function Resetcart() {
  return new Promise(async (resolve) => {
    const response = await fetch("/cart");
    const data = await response.json();
    console.log(data);
    for (let items of data) {
      await deleteCartItem(items);
    }

    console.log({ status: "Cleared" });
    resolve({ status: "Cleared" });
  });
}
