
import { Link, Navigate } from 'react-router-dom'
import React, { useState } from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { deleteCartItemAsync, setCartItems, updateCartItemAsync } from '../features/Cart/cartSlice';
import { useForm } from 'react-hook-form';
import { fetchLoggedInfoUserUpdateAsync  } from '../features/user/userSlice';
import { addOrderAsync, currentorderStatus } from '../features/Order/OrderSlice';
import { selectUserInfo } from '../features/user/userSlice';
import { discountedprice } from '../constant';



const Checkoutpage = () => {

  const {
    register,
    handleSubmit,
    reset,
    formState: { errors },
  } = useForm();
  const items=useSelector(setCartItems);
let totalProducts=0;
let totalAmount=0;

for(let i=0;i<items.length;i++)
{
  totalAmount+=Number(items[i].quanity)*Number(discountedprice(items[i].product));
  totalProducts+=Number(items[i].quanity)
}
const dispatch=useDispatch();
function handlechnage(e,item){
  dispatch(updateCartItemAsync({id:item.id,quanity:+e.target.value}))

}
function handledelete(e,product){
  dispatch(deleteCartItemAsync(product),[e,dispatch])
}
const user=useSelector(selectUserInfo);
const [selectedAddress,setAddress]=useState(null);
const [paymentType,setpaymentType]=useState('cash');
const handleAddress=function(e,index){
  
  setAddress(user.address[index]);
  console.log(selectedAddress);
}
const handleorder=function(e){
  if (selectedAddress && paymentType)
  {
  const order = {
    items,
    totalAmount,
    totalItems:totalProducts,
    user:user.id,
    paymentMethod:paymentType,
    selectedAddress,
    status: 'pending', 
  }
  console.log(order);
  dispatch(addOrderAsync(order))
 
}
else{
  alert('Enter Address and Payment method');
}
}
const curr_product=useSelector(currentorderStatus);

  return (
    <>
 {items.length===0 && <Navigate
 to='/' replace={true}></Navigate>}

  {curr_product && <Navigate
 to={`/product-success/${curr_product.id}`} replace={true}></Navigate>}
    <div className='mx-auto max-w-7xl px-4 sm:px-6 lg:px-8'>
        <div className="grid mx-auto max-w-7xl px-4 sm:px-6 lg:px-8 lg:grid-cols-5">
    <div className=' lg:col-span-3 px-4 bg-white  mt-8 mx-4 mb-12 pb-2'>
    <form 
       noValidate
       onSubmit={handleSubmit((data) => {
        
         dispatch(
          fetchLoggedInfoUserUpdateAsync({...user,address:[...user.address,data],fullName:data.fullName})
         )
         reset()
         
       })}
    >
    <div className='mt-5 grid grid-cols-1 gap-x-6 gap-y-8 '>
      <div className="border-b border-gray-900/10 pb-12">
        <h2 className=" leading-7 text-gray-900 text-3xl  font-bold ">Personal Information</h2>
        <p className="mt-1 text-sm leading-6 text-gray-600">Use a permanent address where you can receive mail.</p>

        <div className="mt-10 grid grid-cols-1 gap-x-6 gap-y-8 sm:grid-cols-6">
          <div className="sm:col-span-3">
            <label htmlFor="first-name" className="block text-sm font-medium leading-6 text-gray-900">
              Full Name
            </label>
            <div className="mt-2">
              <input
                type="text"
                {...register('fullName', {
                  required: 'Write your name please',
                })}
                id="fullName"
               
                className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
              />
            </div>
          </div>


          <div className="sm:col-span-4">
            <label htmlFor="email" className="block text-sm font-medium leading-6 text-gray-900">
              Email address
            </label>
            <div className="mt-2">
              <input
                id="email"
                {...register('email', {
                  required: 'email is required',
                })}
                type="email"
             
                className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
              />
            </div>
          </div>
          <div className="sm:col-span-4">
            <label htmlFor="email" className="block text-sm font-medium leading-6 text-gray-900">
              Phone
            </label>
            <div className="mt-2">
              <input
                id="phone"
                {...register('phone', {
                  required: 'Phone is required',
                })}
                type="tel"
             
                className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
              />
            </div>
            {errors.phone && (
                  <p className="text-red-500 ">{errors.phone.message}</p>
                )}
          </div>
        

          <div className="col-span-full">
            <label htmlFor="street-address" className="block text-sm font-medium leading-6 text-gray-900">
              Street/LocalAddress 
            </label>
            <div className="mt-2">
              <input
                type="text"
                {...register('street', {
                  required: 'Street is required',
                })}
                id="streetAddress"
               
                className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
              />
            </div>
          </div>

          <div className="sm:col-span-2 sm:col-start-1">
            <label htmlFor="city" className="block text-sm font-medium leading-6 text-gray-900">
              City
            </label>
            <div className="mt-2">
              <input
                type="text"
                {...register('city', {
                  required: 'city is required',
                })}
                id="city"
                autoComplete="address-level2"
                className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
              />
            </div>
          </div>

          <div className="sm:col-span-2">
            <label htmlFor="region" className="block text-sm font-medium leading-6 text-gray-900">
              State / Province
            </label>
            <div className="mt-2">
              <input
                type="text"
                {...register('state', {
                  required: 'state is required',
                })}
                id="region"
                autoComplete="address-level1"
                className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
              />
            </div>
          </div>

          <div className="sm:col-span-2">
            <label htmlFor="postal-code" className="block text-sm font-medium leading-6 text-gray-900">
              Pincode 
            </label>
            <div className="mt-2">
              <input
                type="number"
                {...register('pincode', {
                  required: 'Pincode  is required',
                })}
                id="pincode"
               
                className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
              />
            </div>
          </div>
        </div>
      </div>
      <div className="mt-6 flex items-center justify-end gap-x-6">
      <button type="button" className="text-sm font-semibold leading-6 text-gray-900" onClick={e=>reset()}>
        Reset
      </button>
      <button
        type="submit"
        className="rounded-md bg-indigo-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
      >
        Add Address
      </button>
    </div>
      <div className="border-b border-gray-900/10 pb-12">
        <h2 className="text-base font-semibold leading-7 text-gray-900">Address</h2>
        <p className="mt-1 text-sm leading-6 text-gray-600">
          Choose From Existing Address   
        </p>
        <ul role="list">
      {user.address && user.address.map((address,index) => (
       < div className='border-4 border-gray-300'>
          <li key={address.index} className="flex justify-between gap-x-6 py-5 p-2 ">
          
          <div className="flex min-w-0 gap-x-4 ">
           <input

                  name="address"
                  type="radio"
                  onClick={e=>handleAddress(e,index)}
                  className="h-4 w-4 border-gray-300 text-indigo-600 focus:ring-indigo-600 cursor-pointer" 
                  
                />
          
         
            <div className="min-w-0 flex-auto">
            <p className="mt-1 truncate text-sm  font-semibold leading-5 text-gray-900">{address.fullName}</p>  
              <p className="mt-1 truncate text-xs  font-semibold leading-5 text-gray-500">{address.street}</p>
              <p className="text-xs font-semibold leading-6 text-gray-500">{address.pincode}</p>
            </div>
          </div>
          <div className="hidden shrink-0 sm:flex sm:flex-col sm:items-end">
            <p className="text-sm leading-6 text-gray-900">{address.Phone}</p>
            <p className="mt-1 text-xs leading-5 text-gray-500">
                {address.state}
              </p>
            <p className="mt-1 text-xs leading-5 text-gray-500">
                {address.city}
              </p>
          </div>
        </li>
       </div>
      
      
      ))}
    </ul>
    
        <div className="mt-10 space-y-10">
         
          <fieldset>
            <legend className="text-sm font-semibold leading-6 text-gray-900">Payment Method</legend>
            <p className="mt-1 text-sm leading-6 text-gray-600">Choose One.</p>
            <div className="mt-6 space-y-6">
              <div className="flex items-center gap-x-3">
                <input
                  id="card"
                  name="payment"
                  type="radio"
                  checked={paymentType==='card'?true:false}
                  onClick={e=>setpaymentType('card')}
                  className="h-4 w-4 border-gray-300 text-indigo-600 focus:ring-indigo-600"
                />
                <label htmlFor="card" className="block text-sm font-medium leading-6 text-gray-900">
                  Card 
                </label>
              </div>
              <div className="flex items-center gap-x-3">
                <input
                  id="cash"
                  name="payment"
                  type="radio"
                  checked={paymentType==='cash'?true:false}
                  onClick={e=>setpaymentType('cash')}
                  className="h-4 w-4 border-gray-300 text-indigo-600 focus:ring-indigo-600"
                />
                <label htmlFor="c" className="block text-sm font-medium leading-6 text-gray-900">
                  Cash
                </label>
              </div>
              
              
            </div>
          </fieldset>
        </div>
      </div>
    </div>

  
  </form>
    </div>
  <div className='lg:col-span-2'>
  <div className="mx-auto max-w-2xl px-2 py-6 sm:px-2 sm:py-4 lg:max-w-7xl lg:px-4 bg-white mt-8">
<h1 className="text-3xl font-bold tracking-tight text-gray-900 ">Cart</h1>
<div className="mt-8">
          <div className="flow-root">
            <ul role="list" className="-my-6 divide-y divide-gray-200">
              {items.map((item) => (
                <li key={item.product.id} className="flex py-6">
                  <div className="h-24 w-24 flex-shrink-0 overflow-hidden rounded-md border border-gray-200">
                    <img
                      src={item.product.thumbnail}
                      alt={item.product.description}
                      className="h-full w-full object-cover object-center"
                    />
                  </div>

                  <div className="ml-4 flex flex-1 flex-col">
                    <div>
                      <div className="flex justify-between text-base font-medium text-gray-900">
                        <h3>
                          <div href='#'>{item.product.title}</div>
                        </h3>
                        <p className="ml-4">{discountedprice(item.product)}</p>
                      </div>
                      <p className="mt-1 text-sm text-gray-500">{item.product.color}</p>
                    </div>
                    <div className="flex flex-1 items-end justify-between text-sm">
                      <div className='flex justify-start  items-center'>
                      <div className="text-gray-500">
                      <label htmlFor="quantity" className="inline text-sm font-medium leading-6 text-gray-900">
            Qty
          </label>
                      <select className='text-sm m-2 py-1 pr-8' onChange={e=>handlechnage(e,item)} value={item.quanity}> 
                      <option value="1">1</option>
                            <option value="2">2</option>
                            <option value="3">3</option>
                            <option value="4">4</option>
                            <option value="5">5</option>
                      </select>
                      </div>
                     
                      </div>
                      
                      <div className="flex">
                        <button
                          type="button"
                          className="font-medium text-indigo-600 hover:text-indigo-500"
                          onClick={e=>handledelete(e,item)}
                        >
                          Remove
                        </button>
                      </div>
                    </div>
                  </div>
                </li>
              ))}
            </ul>
          </div>
        </div>
        <div className="border-t border-gray-200 px-2 py-6 sm:px-4">
        <div className="flex justify-between text-base font-medium text-gray-900">
          <p>Subtotal</p>
          <p>${totalAmount}</p>
        </div>
        <div className="flex justify-between text-base font-medium text-gray-900">
          <p>Products</p>
          <p>{totalProducts} Products</p>
        </div>
        <p className="mt-0.5 text-sm text-gray-500">Shipping and taxes calculated at checkout.</p>
        <div className="mt-6">
          <div
            onClick={e=>handleorder(e)}
            className="flex items-center justify-center rounded-md border border-transparent bg-indigo-600  py-3 text-base font-medium text-white shadow-sm hover:bg-indigo-700 w-full cursor-pointer"
          >
            Order Now
          </div>
        </div>
        <div className="mt-6 flex justify-start text-center text-sm text-gray-500">
          <p>
            or
            <Link to={'/'}  className="font-medium text-indigo-600 hover:text-indigo-500 ml-4">
              Continue Shopping
              <span aria-hidden="true"> &rarr;</span>
            </Link>
           
          </p>
        </div>
      </div>
</div>

  </div>
    </div>
    </div>
    </>
  
  
 
  )
}

export default Checkoutpage;
