import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import { addCartItem, cartProductsByUserId, deleteCartItem, updateCartItem,Resetcart} from './cartAPI';


const initialState = {
  items: [],
  status: 'idle',
};


export const addCartItemAsync = createAsyncThunk(
  'cart/addCartItemAsync',
  async (product) => {
    const response = await addCartItem(product);
  
    return response.data;
  }
);
export const ResetcartAsync = createAsyncThunk(
  'cart/ResetcartAsync',
  async () => {
    const response = await Resetcart();
    return response.data;
  }
);
export const updateCartItemAsync = createAsyncThunk(
  'cart/updateCartItem',
  async (product) => {
    const response = await updateCartItem(product);
    return response.data;
  }
);
export const cartProductsByUserIdAsync = createAsyncThunk(
  'cart/cartProductsByUserId',
  async () => {
    const response = await cartProductsByUserId();
    return response.data;
  }
);
export const deleteCartItemAsync = createAsyncThunk(
  'cart/deleteCartItem',  
  async (item) => {
    const response = await deleteCartItem(item);
    return response.data;
  }
);

export const counterSlice = createSlice({
  name: 'cart',
  initialState,
 
  reducers: {
    increment: (state) => {
      state.value += 1;
    },
  },

  extraReducers: (builder) => {
    builder
      .addCase(addCartItemAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(addCartItemAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.items.push(action.payload);
      })
      .addCase( cartProductsByUserIdAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase( cartProductsByUserIdAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.items = action.payload;
       
      })
      .addCase( updateCartItemAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase( updateCartItemAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        const index=state.items.findIndex((item)=>item.id===action.payload.id)
        state.items[index] = action.payload;
       
      })
      .addCase( deleteCartItemAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase( deleteCartItemAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        const index=state.items.findIndex((item)=>item.id===action.payload.id)
        state.items.splice(index,1);
       
      })
      .addCase( ResetcartAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase( ResetcartAsync.fulfilled, (state,action) => {
        state.status = 'idle';
        state.items = [];
      })
      ;
  },
});

export const { increment } = counterSlice.actions;
//export const selectCount = (state) => state.counter.value;

export const setCartItems=(state)=>state.cart.items
export default counterSlice.reducer;
