import React, { useEffect } from 'react'
import { useDispatch } from 'react-redux'
import {signOutAsync} from '../features/Auth/AuthSlice'
import { Link } from 'react-router-dom'
import Navbar from '../features/navbar/navbar'
const Logout = () => {
    const dispatch=useDispatch();
    useEffect(()=>{
        dispatch(signOutAsync());
    },[dispatch])
  return (
    <div>
      <Navbar/>
      <main className="grid min-h-full place-items-center bg-white px-6 py-24 sm:py-32 lg:px-8 my-12">
          <div className="text-center">
            <p className="text-base font-semibold text-indigo-600">You've Logged Out Successsfully </p>
            <h1 className="mt-4 text-3xl font-bold tracking-tight text-gray-900 sm:text-5xl">Login again</h1>
            <p className="mt-6 text-base leading-7 text-gray-600">Shop daily with H_mart.</p>
            <div className="mt-10 flex items-center justify-center gap-x-6">
              <Link
                to={'/login'}
                className="rounded-md bg-indigo-600 px-3.5 py-2.5 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
              >
                Login
              </Link>
              
            </div>
          </div>
        </main>
    </div>
  )
}

export default Logout;
