import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import {  fetchLoggedInUserOrder, fetchLoggedInfoUser, updateUserInfo } from './userAPI';

const initialState = {
  userorders:[],
  status: 'idle',
  userinfo:null
};


export const fetchLoggedInUserOrderAsync = createAsyncThunk(
  'counter/fetchLoggedInUserOrderAsync',
  async (userid) => {
    const response = await fetchLoggedInUserOrder(userid);
  
    return response.data;
  }
);
export const fetchLoggedInfoUserAsync = createAsyncThunk(
  'counter/fetchLoggedInfoUserAsync',
  async () => {
    const response = await fetchLoggedInfoUser();
  
    return response.data;
  }
);
export const fetchLoggedInfoUserUpdateAsync = createAsyncThunk(
  'counter/fetchLoggedInfoUserUpdateAsync',
  async (userid) => {
    const response = await updateUserInfo(userid);
    return response.data;
  }
);


export const counterSlice = createSlice({
  name: 'user',
  initialState,
 
  reducers: {
    increment: (state) => {
      state.value += 1;
    },
  },

  extraReducers: (builder) => {
    builder
      .addCase(fetchLoggedInUserOrderAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(fetchLoggedInUserOrderAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.userorders = action.payload;
      })
      .addCase(fetchLoggedInfoUserAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(fetchLoggedInfoUserAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.userinfo = action.payload;
      })
      .addCase(fetchLoggedInfoUserUpdateAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(fetchLoggedInfoUserUpdateAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.userinfo = action.payload;
      })
      ;
  },
});

export const { increment } = counterSlice.actions;
export const selectUserOrder = (state) => state.user.userorders;
export const selectUserInfo = (state) => state.user.userinfo;


export default counterSlice.reducer;
