import React, { useEffect, useState } from 'react'
import { useDispatch,useSelector } from 'react-redux'
import { ITEM_PER_PAGE, discountedprice } from '../../../constant';

import {ArrowDownIcon, ArrowUpIcon, EyeIcon,PencilIcon} from "@heroicons/react/24/outline";
import { TotalOrders, fecthAllOrdersAsync, orderlist, updateOrderAsync } from '../../Order/OrderSlice';
const Adminorders = () => {
    const [page,setpage]=useState(1);
    const [editableid,seteditableid]=useState(-1);
    const orders=useSelector(orderlist);
    const totalorders=useSelector(TotalOrders);
    console.log(totalorders)
    const dispatch=useDispatch();
    const handlepage=()=>{
        setpage(page);
    }
    const [sort,setsort]=useState({})

    const handleSort = (option) => {
      console.log(option)
    const newsort = { _sort: option.sort, _order: option.order };
    console.log({newsort});
    setsort(newsort);
  };
    useEffect(()=>{
      const pagination = { _page: page, _limit: ITEM_PER_PAGE };
      dispatch(fecthAllOrdersAsync({ sort,pagination }));
      },[dispatch,page,sort]);
     


      const handleEdit=(e,order)=>{
            seteditableid(order.id)
      }
      const handleEye=()=>{


      }
      const handlechange=(e,order)=>{
        const updatedorder={...order,status:e.target.value};
        dispatch(updateOrderAsync(updatedorder))
        seteditableid(-1);
      }
      const chooseColor = (status) => {
        switch (status) {
          case 'pending':
            return 'bg-purple-200 text-purple-600';
          case 'dispatched':
            return 'bg-yellow-200 text-yellow-600';
          case 'delivered':
            return 'bg-green-200 text-green-600';
          case 'cancelled':
            return 'bg-red-200 text-red-600';
          default:
            return 'bg-purple-200 text-purple-600';
        }
      };
  return (
    <div>
      <>
  <div className="overflow-x-auto">
    <div className="min-w-screen min-h-screen flex items-center justify-center bg-gray-100 font-sans overflow-hidden">
      <div className="w-full">
        <div className="bg-white shadow-md rounded my-6">
          <table className="min-w-max w-full table-auto">
            <thead>
              <tr className="bg-gray-200 text-gray-600 uppercase text-sm leading-normal">
              <th
                    className="py-3 px-6 text-left cursor-pointer"
                    onClick={(e) =>
                      handleSort({
                        sort: 'id',
                        order: sort?._order === 'asc' ? 'desc' : 'asc',
                      })
                    }
                  >
                    Order# {' '}
                    {sort._sort === 'id' &&
                      (sort._order === 'asc' ? (
                        <ArrowUpIcon className="w-4 h-4 inline"></ArrowUpIcon>
                      ) : (
                        <ArrowDownIcon className="w-4 h-4 inline"></ArrowDownIcon>
                      ))}
                  </th>
                <th className="py-3 px-6 text-left">Items</th>
                <th
                    className="py-3 px-6 text-left cursor-pointer"
                    onClick={(e) =>
                      handleSort({
                        sort: 'totalAmount',
                        order: sort?._order === 'asc' ? 'desc' : 'asc',
                      })
                    }
                  >
                    Total Amount {' '}
                    {sort._sort === 'totalAmount' &&
                      (sort._order === 'asc' ? (
                        <ArrowUpIcon className="w-4 h-4 inline"></ArrowUpIcon>
                      ) : (
                        <ArrowDownIcon className="w-4 h-4 inline"></ArrowDownIcon>
                      ))}
                  </th>
                <th className="py-3 px-6 text-center">Address</th>
                <th className="py-3 px-6 text-center">Status</th>
                <th className="py-3 px-6 text-center">Actions</th>
              </tr>
            </thead>
            <tbody className="text-gray-600 text-sm font-light">
             {orders.map(order=> <tr className="border-b border-gray-200 hover:bg-gray-100">
                <td className="py-3 px-6 text-left whitespace-nowrap">
                  <div className="flex items-center">
                    <div className="mr-2">
                      <svg
                        xmlns="http://www.w3.org/2000/svg"
                        x="0px"
                        y="0px"
                        width={24}
                        height={24}
                        viewBox="0 0 48 48"
                        style={{ fill: "#000000" }}
                      >
                       
                        <circle cx={24} cy={24} r={4} fill="#80deea" />
                      </svg>
                    </div>
                    <span className="font-medium">{order.id}</span>
                  </div>
                </td>
                <td className="py-3 px-6 text-left">
                  {order.items.map(item=>
                  <div className="flex items-center">
                     <div className="mr-2">
                            <img
                              className="w-10 h-10 rounded-full"
                              src={item.product.thumbnail}
                             alt='descri'/>
                          </div>
                    <span className='font-bold'>{item.title} - ${discountedprice(item.product)}- {item.quanity}</span>
                  </div>)}
                </td>
                <td className="py-3 px-6 text-center">
                  <div className="flex items-center justify-center">
                    <span className=' font-semibold'>{order.totalAmount}₹</span>
                  </div>
                  
                </td>
                <td className="py-3 px-6 text-center">
                  <div className=" items-center justify-center">
                      <div>
                      <span className=' font-bold'>{order.selectedAddress.fullName}</span>
                      </div>
                      <div>
                      <span className=' font-semibold'>{order.selectedAddress.street}</span>
                      </div>
                      <div>
                      
                      <span className=' font-semibold'>{order.selectedAddress.state}</span>
                      </div>
                      <div>
                      <span className=' font-semibold'>{order.selectedAddress.pincode}</span>
                      </div>
                      <div>
                      <span className=' font-semibold'>{order.selectedAddress.phone}</span>
                      </div>

                  </div>
                  
                </td>
                <td className="py-3 px-6 text-center">
                  {order.id===editableid?(
                      <div>
                      <select onChange={e=>handlechange(e,order)} value={' '}>
                          <option value="pending">Pending</option>
                          <option value="dispatched">Dispatched</option>
                          <option value="delivered">Delivered</option>
                          <option value="cancelled">Cancelled</option>
                      </select>
                    </div>
                  ):(
                   <span className={`${chooseColor(order.status)} py-1 px-3 rounded-full text-xs`} >
                   {order.status}
                 </span>
                  )}
                 
                  
                </td>
                <td className="py-3 px-6 text-center">
                  <div className="flex item-center justify-center">
                    
                   <div className='m-2 cursor-pointer  hover:text-purple-600 font-bold'>
                    <EyeIcon className='w-6 h-6'onClick={e=>handleEye(e,order)}/>
                   </div>
                   <div className='m-2 cursor-pointer  hover:text-red-600'><PencilIcon className='w-6 h-6' onClick={e=>handleEdit(e,order)}/></div>
                    
                  </div>
                </td>
              </tr>)}
            
            </tbody>
          </table>
        </div>
        <Pagination  page={page} setpage={setpage} limit={ITEM_PER_PAGE} total={totalorders} handlepage={handlepage}></Pagination>
      </div>
    </div>
  </div>
</>

    </div>
  )
}
function Pagination({page,setpage,limit,total,handlepage}) {
  return (
    <div className="flex items-center justify-between border-t border-gray-200 bg-white px-4 py-3 sm:px-6">
      <div className="flex flex-1 justify-between sm:hidden">
        <div
          onClick={e=>page>1?setpage(page-1):1}
          className="relative inline-flex items-center rounded-md border border-gray-300 bg-white px-4 py-2 text-sm font-medium text-gray-700 hover:bg-gray-50"
        >
          Previous
        </div>
        <div
           onClick={e=>page<Math.ceil(total / limit)?setpage(page+1):1}
          className="relative ml-3 inline-flex items-center rounded-md border border-gray-300 bg-white px-4 py-2 text-sm font-medium text-gray-700 hover:bg-gray-50"
        >
          Next
        </div>
      </div>
      <div className="hidden sm:flex sm:flex-1 sm:items-center sm:justify-between">
        <div>
          <p className="text-sm text-gray-700">
            Showing <span className="font-medium">{(page-1)*limit+1}</span> to{' '}
            <span className="font-medium">{((page)*limit)>total?total:(page)*limit}</span> of{' '}
            <span className="font-medium">{total}</span> results
          </p>
        </div>
        <div>
          <nav
            className="isolate inline-flex -space-x-px rounded-md shadow-sm"
            aria-label="Pagination"
          >
          
            {/* Current: "z-10 bg-indigo-600 text-white focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600", Default: "text-gray-900 ring-1 ring-inset ring-gray-300 hover:bg-gray-50 focus:outline-offset-0" */}
            {
            Array.from({ length: Math.ceil(total / limit)}).map((el, index) => {
              const pageNumber = index + 1;
              return (
                <div
                  
                  key={index}
                  href="#"
                  aria-current="page"
                  aria-label={`Page ${pageNumber}`}
                  className={`relative z-10 inline-flex items-center  ${page===index+1?"  bg-indigo-600 text-white":"bg-white text-black" } px-4 py-2 text-sm font-semibold  focus:z-20 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600 cursor-pointer`}
                  onClick={e=>handlepage(index+1)}
                >
                  {pageNumber}
                </div>
              );
            })
            }
          
          </nav>
        </div>
      </div>
    </div>
  );
}
export default Adminorders
