import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import { addOrder, fetchallorders, updateOrder } from './OrderAPI';

const initialState = {
  orderList:[],
  status: 'idle',
  currentorder:null,
  totalorders:0,
};


export const addOrderAsync = createAsyncThunk(
  'counter/addOrder',
  async (order) => {
    const response = await addOrder(order);
  
    return response.data;
  }
);
export const updateOrderAsync = createAsyncThunk(
  'counter/updateorder',
  async (order) => {
    const response = await updateOrder(order);
    return response.data;
  }
);
export const fecthAllOrdersAsync = createAsyncThunk(
  'counter/fetchallorders',
  async ({sort,pagination}) => {
    const response = await fetchallorders(sort,pagination);
  
    return response.data;
  }
);

export const counterSlice = createSlice({
  name: 'order',
  initialState,
 
  reducers: {
    resetCurrentOrder: (state) => {
      state.currentorder=null
    },
  },

  extraReducers: (builder) => {
    builder
      .addCase(addOrderAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(addOrderAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.orderList.push(action.payload);
        state.currentorder=action.payload;
      })
      .addCase(fecthAllOrdersAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(fecthAllOrdersAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.orderList = action.payload.orders;
        state.totalorders=state.orderList.length;
      })
      .addCase(updateOrderAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(updateOrderAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        const index=state.orderList.findIndex(order=>order.id===action.payload.id);
        state.orderList[index]=action.payload
      });
  },
});

export const { resetCurrentOrder } = counterSlice.actions;
export const Allorders = (state) => state.order.orderList;
export const currentorderStatus = (state) => state.order.currentorder;
export const orderlist = (state) => state.order.orderList;
export const TotalOrders = (state) => state.order.totalorders;

export default counterSlice.reducer;
